import axios from 'axios'
import protoRoot from '@/proto/proto'
import protobuf from 'protobufjs'
import pako from 'pako'
import store from '../store'
const request = axios.create({
  timeout: 45000,
  method: 'post',
  headers: {
    'X-Requested-With': 'XMLHttpRequest',
    'dating-user-id': '',
    'dating-route-id': '',
    'Content-Type': 'application/x-protobuf',
    'Accept': 'application/x-protobuf',
    'Authorization': '' ,
  },
  responseType: 'arraybuffer'
})
let PBMessageRequest = '';
let Package = '';
let Head = '';
let cmd = '';
request.interceptors.request.use(
  config => {
    const headMsg  = Head.create({cmd:3});
    const message = PBMessageRequest.create(config.data);
    const resBuffer = PBMessageRequest.encode(message).finish();
    const msg = Package.create({head:headMsg,body:resBuffer})
    const resBuffer2 = Package.encode(msg).finish();
    config.data = new Blob([resBuffer2], {type: 'buffer'});
    config.headers['dating-route-id'] = cmd;
    if(store.state.userInfo.token){
      config.headers['dating-user-id'] = store.state.userInfo.profile.uuid;
      config.headers['Authorization'] = 'Bearer ' + store.state.userInfo.token;
    }
    return config
  },
  err => {
    return Promise.reject(err)
  })
request.interceptors.response.use(
  response => {
    const res = protoRoot.lookupType('com.jdating.account.SignInResp');
    const resData = Package.decode(protobuf.util.newBuffer(response.data));
    let body ='';
    if(resData.head.compressMethod==1){
      body = protobuf.util.newBuffer(pako.inflate(resData.body))
    } else {
      body = protobuf.util.newBuffer(resData.body)
    }
    return res.decode(body)
    //拦截响应，做统一处理 
    
  },
  //接口错误状态处理，也就是说无响应时的处理
  error => {
    return Promise.reject(error) // 返回接口返回的错误信息
  })
export default {
  post(params,protoName){
      PBMessageRequest = protoRoot.lookupType(protoName+'Req');
      cmd = protoRoot.lookupEnum('CsCmd').values['Cmd_Cs' + protoName+'Req'];
      Package = protoRoot.lookupType('com.jdating.common.Package');
      Head = protoRoot.lookupType('com.jdating.common.Head');
      return new Promise((resolve,reject)=>{
        request.post('https://www.kinkylife.net/api/', params).then(res =>{
          resolve(res)
        },err => {
          reject(err)
        })
      })
  }
}