<template>
<div class="content">
    <div class="content__side-bar" v-if="displayMenu">
        <sideBar></sideBar>
    </div>
    <div class="content__main">
        <div class="content__main--title">
            <div class="header__title">
                <div class="meets__title">
                    <button class="meets__title--menu" @click="switchMenu">
                        <img class="coverImg" src="../assets/icon/menu.jpg" alt="">
                    </button>
                    <router-link to="meet_account">
                        <h2 class="meets__title--item active">Meet</h2>
                    </router-link>
                    <router-link to="meets">
                        <h2 class="meets__title--item">Likes</h2>
                    </router-link>
                    <button class="meets__title--filter">
                        <img class="coverImg" src="../assets/icon/filter.png" alt="">
                    </button>
                </div>
            </div>
        </div>
        <div class="content__main--content">
            <single-card></single-card>
        </div>
        <van-popup v-model="show" class="match">
            <div class="match__main">
                <img class="match__icon coverImg" src="https://via.placeholder.com/365x65" alt="">
                <p class="match__desc">You & Kevin liked each other</p>
                <img class="match__logo coverImg" src="https://via.placeholder.com/300x120" alt="">
                <div class="match__fun">
                    <el-button>Say HI</el-button>
                    <a class="match__continue" href="#">Continue To Play</a>
                </div>
            </div>
        </van-popup>
    </div>
    <div class="overlay" v-if="showOverlay" @click="hidePopup"></div>
</div>
</template>

<script>
import SideBar from '../components/sidebar.vue';
import SingleCard from '../components/card/single-card.vue';
export default {
    name: 'meetAccount',
    components: {
        SideBar,
        SingleCard,
    },
    computed: {
        displayMenu() {
            return (document.body.clientWidth < 768 &&
                    !this.showOverlay) ? false : true
        }
    },
    data() {
        return {
            showOverlay: false,
            showMenu: true,
            show: true,
        }
    },
    methods: {
        switchMenu() {
            this.showOverlay = true;
        },
        hidePopup() {
            this.showOverlay = false;
        },
        gotoFilter() {
            this.$router.push({
                path: 'filter'
            })
        }
    }
}
</script>

<style lang="scss">
.match {
    &__main {
        width: 600px;
        height: auto;
        padding: 60px;
        text-align: center;
        box-sizing: border-box;
        background: rgba($color: #000000, $alpha: 0.3);
        @media screen and (max-width: 768px) {
            width: 300px;
            padding: 40px;
        }
    }
    .coverImg {
        width: 300px;
        margin-bottom: 20px;
         @media screen and (max-width: 768px) {
            width: 200px;
        }
    }
    &__desc {
        font-size: 16px;
        color: #ffffff;
        margin-bottom: 40px;
    }
    &__continue {
        font-size: 16px;
        color: #ffffff;
        display: block;
        margin-top: 20px;
    }
}
</style>