<template>
 <div>
	<Header></Header>
	<div class="login">
		<div class="login__block"></div>
		<el-form class="login__form" label-position="top" :model="loginForm" :rules="rules" ref="loginForm">
			<div class="login__form-boxs boxs">
				<h2>Welcome to here</h2>
				<p>A serious community for <strong>BDSM</strong> people</p>
				<el-form-item label="Email Address" prop="email">
					<el-input v-model="loginForm.email"></el-input>
				</el-form-item>
				<el-form-item label="Password" prop="password">
					<el-input v-model="loginForm.password" type="password"></el-input>
				</el-form-item>
				<div class="login__form-foot">
					<el-row>
						<el-col :span="12">
							<el-checkbox v-model="loginForm.remember">Remember me</el-checkbox>
						</el-col>
						<el-col :span="12" class="login__form-foot-forget">
							<span>Forget password?</span>
						</el-col>
					</el-row>
				</div>
			</div>
		</el-form>
		<div class="login__submit">
			<el-button type="primary" @click="signIn">Sign In</el-button>
			<p>Aleady a member? <span>Sign In</span></p>
		</div>
	</div>
	<Footer></Footer>
 </div>
</template>
<script>
import Header from '@/components/header';
import Footer from '@/components/footer';
export default {
 name: 'HelloWorld',
 components:{
	Header,
	Footer
 },
 data(){
	return {
		loginForm:{
			email:'anna9311',
			password:'123456',
			remember: false
		},
		rules: {
          email: [
            { required: true, message: 'please enter email', trigger: 'blur' },
          ],
          password: [
            { required: true, message: 'please enter password', trigger: 'blur' }
		  ],
		} 
	}
 },
 methods: {
	signIn() {
		this.$refs.loginForm.validate((valid) => {
			if (valid) {
				// // 接口调用
				this.$request.post(this.loginForm,'SignIn').then((data)=>{
					this.$store.dispatch('login',data.info);
					console.log(this.$store)
				})
				
			} else {
				return false;
			}
        });
	},
 },
 created () {
 }
}
</script>
<style lang="scss">
	.login{
		text-align: left;
		position: relative;
		color: #101327FF;
		&__block{
			background:rgba(16,19,39,1);
			height: 50px;
		}
		&__form{
			&-boxs{
				position: relative;
				top: -50px;
				.el-checkbox__inner{
					border-radius: 100%;
				}
				h2{
					font-size: 24px;
					margin-bottom: 10px;
				}
				p{
					font-size: 16px;
					margin-bottom: 20px;
					strong{
						font-weight: 600;
					}
				}
			}
			&-foot{
				&-forget{
					text-align: right;
					cursor: pointer;
					font-weight:bold;
				}
			}
		}
		&__submit{
			text-align: center;
			margin-bottom: 50px;
			.el-button{
				background-color: #FF00FFFF;
    			border-color: #FF00FFFF;
				width: 40%;
			}
			p{
				display: none;
				@media screen and (max-width: 768px) {
					display: block;
					margin-top: 10px;
					span{
						color: #FF00FFFF;
					}
				}
			}
		}
	}
</style>
